.btn-secondary {
  background: transparent;
  color: var(--accent);
  border: 1px solid var(--accent);
  padding: 0.5rem 1rem;
  border-radius: var(--radius-md);
  font-weight: 500;
  margin-top: 1rem;
  cursor: pointer;
  transition: all 0.2s;
}
.btn-secondary:hover:enabled {
  background: var(--accent);
  color: white;
}
